import { IndexDefinitionType } from "./IndexDefinitionType";
export class IndexDefinition {
    constructor() {
        this.indexColumns = [];
        this.indexType = IndexDefinitionType.INDEX;
    }
    static create() {
        return new IndexDefinition();
    }
    name(name) {
        this.indexName = name;
        return this;
    }
    columns(...columns) {
        this.indexColumns.push(...columns);
        return this;
    }
    type(type) {
        this.indexType = type;
        return this;
    }
    get() {
        if (!this.indexColumns.length)
            throw new Error("No columns defined for index");
        let indexDefinition = this.indexName && this.indexType === IndexDefinitionType.UNIQUE ? `${this.indexType} INDEX` : this.indexType;
        if (this.indexName)
            indexDefinition += ` \`${this.indexName}\``;
        indexDefinition += ` (${this.indexColumns.map(column => `\`${column}\``).join(", ")})`;
        return indexDefinition;
    }
}
